/* eslint-disable react-hooks/exhaustive-deps */
// #region Imports
import {
  Button,
  Col,
  Container,
  Form,
  Row,
} from 'react-bootstrap';
import classNames from 'classnames';
import { useEffect, useRef, useState } from 'react';
import {
  useAppDispatch,
} from '../../redux/reduxHooks/reduxHooks';
import { useIssues } from '../../redux/selectors';
import loadIssues from '../../utils/helpers/loadIssues';
// #endregion

const InputArea: React.FC = () => {
  const dispatch = useAppDispatch();
  const { error } = useIssues();

  const [repoLink, setRepoLink] = useState('');
  const [isWriting, setIsWriting] = useState(false);

  const inputRef = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    const repoLinkInStorage = localStorage.getItem('repoLink');

    if (repoLinkInStorage) {
      setRepoLink(repoLinkInStorage);
    }

    if (inputRef.current) {
      inputRef.current.focus();
    }
  }, []);

  const load = () => loadIssues({
    setIsWriting,
    repoLink,
    dispatch,
  });

  useEffect(() => {
    if (!isWriting) {
      load();
    }
  }, [repoLink]);

  return (
    <Container className="mx-auto mt-5">
      <Row>
        <Col xs={10}>
          <Form.Control
            ref={inputRef}
            type="text"
            placeholder="Enter repo URL"
            className={classNames('border rounded-0', {
              'border-danger': error,
              'border-dark': !error,
            })}
            value={repoLink}
            onChange={(e) => {
              setRepoLink(e.target.value.trim());
              setIsWriting(true);
            }}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                load();
              }
            }}
          />
        </Col>
        <Col>
          <Button
            className="w-100 h-100 border
            border-dark rounded-0 btn btn-light"
            onClick={load}
          >
            Load
          </Button>
        </Col>
      </Row>

      {error && (
        <p className="text-danger  mt-3">
          {error}
        </p>
      )}
    </Container>
  );
};

export default InputArea;
